import * as ST from './styled'
import { FC, useState } from 'react';
import { Button, Input } from '@src/components';
import { useAuth } from '@providers/AuthProvider';
import { useFormik } from 'formik';
import { handleFormError } from '@src/utils/toast';
import * as yup from 'yup';

interface LoginForm {
  email: string;
  password: string;
}

let VALIDATION_SCHEMA = yup.object().shape({
  email: yup.string().required('Обязательное поле'),
  password: yup.string().required('Обязательное поле'),
});

const Login: FC = () => {
  const { login } = useAuth();
  const [isLoading, setIsLoading] = useState(false);

  const form = useFormik<LoginForm>({
    initialValues: {
      password: '',
      email: '',
    },
    validationSchema: VALIDATION_SCHEMA,

    onSubmit: async (values) => {
      try {
        setIsLoading(true);
        await login(values.email, values.password)
      } catch (e) {
        handleFormError(e, form)
      } finally {
        setIsLoading(false);
      }
    },
  });

  return (
    <ST.Wrapper>
      <ST.Form onSubmit={form.handleSubmit}>
        <ST.Header>
          <ST.Logo />
          <ST.Title>Admin / Авторизация</ST.Title>
        </ST.Header>
        <Input
          name="email"
          onChange={form.handleChange}
          value={form.values.email}
          caption="Имя пользователя"
          isError={Boolean(form.errors.email)}
          errorText={form.errors.email}
          onBlur={form.handleBlur}
        />
        <Input
          name="password"
          onChange={form.handleChange}
          value={form.values.password}
          caption="Пароль"
          type="password"
          isError={Boolean(form.errors.password)}
          errorText={form.errors.password}
          onBlur={form.handleBlur}
        />
        <Button text="Войти" isLoading={isLoading} />
      </ST.Form>
    </ST.Wrapper>
  )
}

export default Login;
